<template>
  <div id="app">
    <NewPost @post-created="addPost" />
    <PostList :posts="posts" />
  </div>
</template>

<script>
import NewPost from './components/NewPost.vue';
import PostList from './components/PostList.vue';

export default {
  components: {
    NewPost,
    PostList
  },
  data() {
    return {
      posts: []
    };
  },
  methods: {
    addPost(post) {
      this.posts.push(post);
    }
  }
};
</script>
